#intro {
  height: calc(100vh - 4rem);
  width: 100vw;
  max-width: 85rem;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}


.bg {
  position: absolute;
  top: -4rem;
  right: 0;
  z-index: -1;
  object-fit: cover;
  height: 100vh;
}

.left-intro {
  height: 100vh;
  width: 100vw;
  padding: 2rem;
  font-size:3rem ;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hello {
  font-size: 1.4rem;
  font-weight: 400;
  margin-bottom: .9rem;
}

.introName {
  line-height: 4rem;

}

.introNameColor {
  color:  rgb(254, 85, 0);
}

.introParagraph {
  font-size: .9rem;
  /* font-size: small; */
  font-weight: 100;
  margin: .5rem 0;
  line-height: 1.5rem;
}

.introHireMe {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1rem 0;
  padding: 0.75rem 2rem;
  background: rgb(197, 197, 197);
  border-radius: 3rem;
  font-weight: bold;
  /* border: 1px solid rgb(254, 85, 0); */
  transition: transform 0.3s, background-color 0.3s;
}

.introHireMe:hover {
  cursor: pointer;
  transform: translateY(-5px);
  background-color: white;
 
  
  
}

.introBtnImg{
  margin: 0 0.3rem 0 0;
  object-fit: cover;
  height: 1rem;
}

@media screen and (max-width: 400px) {
  .bg {
    right: -10vw;
  }
  .introName {
    font-size: 8vw;
    line-height: 6vh;
  }
  .hello {
    font-size: 4vw;
    margin-bottom: .5rem;
  }
}